		margin-right: 0
		
	.companyMessage,.world
		margin-top: 0
		
	.companyMessage,.userMessage
		margin-left: 0
		.msgBoard
			padding: 14px 16px
			display: flex
			display: webkif-flex
			.logo
				flex-basis: 200px
				padding-right: 50px
